<template>
    <div v-if="jwt" class="card content users">
        <h2>Bienvenue, {{currentUser.name}}</h2>
        <p v-if="currentUser.level ==1">En tant d'utilisateur, vous pouvez uniquement créer un auteur ou rajouter une œuvre. Naviguez jusqu'à un auteur pour ajouter une œuvre.</p>
        <p v-if="currentUser.level ==2">En tant que rédacteur, vous pouver créer, modifier et supprimer une fiche (auteur ou œuvre). Naviguez jusqu'à un auteur pour ajouter une œuvre</p>
        <button @click="$router.push('/new/person')">Ajouter un auteur</button>
        <button @click="$router.push('/dashboard')" v-if="currentUser.level >=3">Accéder au tableau de bord</button>
        <button @click="document.location.reload()" class="outline">Se déconnecter</button>
    </div>
</template>
<script>
import { mapGetters } from 'vuex'

export default {
    name: 'home',
    computed: {
        ...mapGetters('users',[
            'currentUser'
        ]),
        ...mapGetters('jwt',[
            'jwt'
        ])
    }
}
</script>
