    outline: none;
    transition: all .3s;
    user-select: none;
    cursor: pointer;
    display: inline-block;
    border-radius: 7px;
    border: none;
    font-size: 15px;
    background: var(--b3);
    border: 1px solid var(--b3);
    color: var(--b8);
    margin: 10px 15px 0 0;
    padding: 10px 16px;
}

button.outline {
    background: none;
    color: var(--b3);
}
button:hover { 
    border: 1px solid var(--b2);
