<template>
    <div class="person" itemscope itemtype="http://schema.org/Thing">
        
        <div class="image">
            
            <img target="blank" :src="img" itemprop="image" :alt="'image de '+name" @error="imageUrlAlt" @click="$router.push('/'+slug)">
            <div class="classrooms">
                <ClassroomLabel classroom="1"/>
                <ClassroomLabel classroom="2"/>
                <ClassroomLabel classroom="3"/>
            </div>
        </div>
        <StateDisplay :state="state" class="normal"/>
        <div class="content" itemprop="name">
            <router-link :to="slug">{{name}}</router-link>
            <div class="actions"  v-if="jwt && level >= 2">
                <img src="@/assets/icon/edit.svg" @click="$router.push('/'+slug+'/edit/')" class="action" >
                <img src="@/assets/icon/delete.svg" @click="deletePerson(slug)" class="action">
            </div>
        </div>
    </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex'
import StateDisplay from '@/components/state/Display.vue'
import ClassroomLabel from '@/components/classroomLabel.vue'


export default {
    props: ['id','slug', 'name','img','bio', 'state'],
    components: {
        StateDisplay,
        ClassroomLabel
    },
    computed: {
        ...mapGetters('jwt',[
        'jwt',
        'level',
        'jwtSubject',
        'jwtIssuer'
        ])
    },
    methods: {
        ...mapActions('persons',[
            `deletePerson`
        ]),
        imageUrlAlt(event) {
            if (!event.target.src.includes("/img/persons/default.webp")) event.target.src = "/img/persons/default.webp"
        }
    }
}
</script>